import React, { Component } from "react"
import leafSmall from "../../static/leaf-small.png"
import { doc } from "prettier"

class Menu extends Component {
  constructor(props) {
    super(props)
  }
  componentDidMount() {
    window.addEventListener("scroll", this.brandScroll)
  }

  brandScroll = () => {
    var brandDiv = document.getElementsByClassName("brand-div")[0]
    var leafBox = document.getElementsByClassName("brand-leaf-box")[0]
    var height = document.getElementsByClassName("landing-bkgr")[0].offsetHeight
    var y = window.scrollY
    if (y <= height) {
      brandDiv.classList = "brand-div hide"
      leafBox.classList = "brand-leaf-box hide"
    } else {
      brandDiv.classList = "brand-div appear"
      leafBox.classList = "brand-leaf-box appear"
    }
  }
  
  scrollyDown = (className) => {
    switch(className) {
      case "about-menu":
        let scrollHeight = document.querySelectorAll(".about-div")[0].offsetHeight - 45;
        window.scrollTo({
          top: scrollHeight,
          behavior: "smooth"
        });
        document.getElementById("about-menu").style.backgroundImage = 'url(/static/menustroke.png)'

        break;
      case "process-menu":
        break;
      case "portfolio-menu":
        break;
      case "blog-menu":
        break;
    }
  }

  render() {
    let menu = (
      <nav className="nav flex-column">
        <div className="brand-div hide">
          <div className="brand-box">
            <div className="brand-title">Luiz Botega</div>
            <div className="brand-subtitle">service designer</div>
          </div>
          <div className="brand-leaf-box hide">
            <img className="brand-leaf" src={leafSmall}></img>
          </div>
        </div>
        <div id="about-menu" className="a nav-item-box" onClick={() => this.scrollyDown("about-menu")}>
          <a className="nav-link">
            who am i
          </a>
        </div>
        <div id="process-menu" className="b nav-item-box" onClick={() => this.scrollyDown("process-menu")}>
          <a className="process-menu nav-link">
            my process
          </a>
        </div>
        <div id="portfolio-menu" className="c nav-item-box" onClick={() => this.scrollyDown("portfolio-menu")}>
          <a className="portfolio-menu nav-link">
            my works
          </a>
        </div>
        <div id="blog-menu" className="d nav-item-box" onClick={() => this.scrollyDown("blog-menu")}>
          <a className="blog-menu nav-link">
            my texts
          </a>
        </div>
      </nav>
    )
    return menu
  }
}

export default Menu
